.homeContainer {
  display: grid;
  grid-template-columns: 3fr 2fr;
  justify-content: space-around;
  align-items: center;
}

.homeContainer section {
  width: 100%;
  height: 100%;

  background-color: var(--selection);
  background-size: cover;
  clip-path: polygon(0% 0%, 0% 100%, 100% 100%, 50% 0%);
}

.homeContainer section .photoCard {
  width: 350px;
  height: 350px;

  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;

  background: var(--selection);
  box-shadow: 10px 10px 15px rgba(25, 22, 34, 0.6),
    -10px -10px 15px rgba(25, 22, 34, 0.6);
}

.photoCard.back {
  position: absolute;
  left: 1.8%;
  top: 12%;
}

.photoCard.front {
  position: absolute;
  top: 40%;
  left: 14%;
}

.homeContainer section .photoCard img {
  width: 100%;
  height: 100%;
}

.homeContainer section .photoCard img.cropImage {
  object-fit: cover;
  object-position: 0 100%;
}

.homeContainer main {
  position: relative;
  margin: 0;

  display: flex;
  flex-direction: column;
  height: 100%;

  justify-content: flex-start;
  align-items: center;
}

.homeContainer main div {
  display: flex;
  flex-direction: column;

  height: 50vh;
  padding: 6%;

  justify-content: space-evenly;
  align-items: flex-end;
  text-align: right;

  line-height: 2rem;
}

.homeContainer main div h2 {
  font-size: 2.5em;
  font-weight: 700;
}

.homeContainer main div p {
  font-size: 1.5em;
  color: #737380;
}

.homeContainer main img {
  position: absolute;
  z-index: -1;
  bottom: 0;
  right: 0;

  mix-blend-mode: difference;
}
